html{scroll-behavior:smooth}#articleControls{display:flex;justify-content:end;align-items:center;gap:1em}.title{font-weight:350;font-size:3em;overflow-wrap:break-word;text-align:center}.dateView{color:#bfbfbf;font-size:.875em;display:flex;justify-content:center;align-items:center;margin:2em}.dateView p{margin:0;line-height:100%}@media screen and (min-width: 700px){.dateView>:not(:last-child){display:flex;align-items:center}.dateView>:not(:last-child)::after{content:"·";font-size:1.75rem;padding:0 .5rem}}@media screen and (max-width: 700px){.dateView{flex-flow:column;gap:1em}}.article{width:100%;overflow-wrap:break-word;line-height:1.6em;margin-bottom:15%}.article a{color:#22a}.article a:visited{color:#666}.article a:hover{color:purple}.article h1,.article h2,.article h3,.article h4,.article h5,.article h6{font-weight:400;padding-top:2em;font-weight:350;padding-bottom:.5em;padding-top:.5em;border-bottom:1px solid #bbb;color:#666}img{max-width:100%}.floatCenter{display:block;margin:auto}@media only screen and (min-width: 992px){.floatLeft{float:left}.floatRight{float:right}}.noWrapAroundText{float:none;clear:none}.captionBox{width:auto;display:grid;border:1px solid gray;margin:.3em}.captionImagePreview{border:1px solid gray;display:block;margin:auto;margin-top:3px}.captionBoxInner{line-height:1.1em;font-size:.7em;margin:.3em}